// Basic Buttons

.btn-primary, .btn-info, .btn-success, .btn-warning, .btn-danger {
  border: 0;
}

.btn-danger:hover {
  background: @brand-danger;
  color: white;
}

// Outline Buttons
.button-outline-variant(@color; @activeTextColor: #fff) {
    color: @color;
    background-color: transparent;
    border-color: @color;

    &:focus,
    &.focus,
    &:hover,
    &:active,
    &.active,
    .open > .dropdown-toggle& {
        color: @activeTextColor;
        background-color: @color;
        box-shadow: none;
    }

    &.disabled,
    &[disabled],
    fieldset[disabled] & {
        &,
        &:hover,
        &:focus,
        &.focus,
        &:active,
        &.active {
            border-color: @color;
        }
    }
}

.btn-default-outline {
  .button-outline-variant(@btn-default-color);
}

.btn-primary-outline {
  .button-outline-variant(@btn-primary-border);
}

.btn-success-outline {
  .button-outline-variant(@btn-success-border);
}

.btn-info-outline {
  .button-outline-variant(@btn-info-border);
}

.btn-warning-outline {
  .button-outline-variant(@btn-warning-border);
}

.btn-danger-outline {
  .button-outline-variant(@btn-danger-border);
}

// File Upload Button
.btn-upload {
  overflow: hidden;
  position: relative;

  input[type="file"] {
    cursor: pointer;
    margin: 0;
    opacity: 0;
    padding: 0;
    position: absolute;
    right: 0;
    top: 0;
  }
}


// Other Button Utilities
.btn-table-align {
    padding-top: @padding-base-vertical + 1px;
    padding-bottom: @padding-base-vertical + 1px;
}

.fa-btn {
    .m-r-xs;
}

.btn:focus {
  outline: none !important;
}

.btn-pretty {
  background: linear-gradient(to right, #62BFAD, #B7E3E4);
  color: @brand-primary;
}
.btn-pretty:hover {
  color: @brand-secondary;
  background: linear-gradient(to right, #864BFF, #00FFCB);
}

.btn-bright {
  background: linear-gradient(to right, #EED974, #FDF06F);
  color: black;
}

.btn-bright:hover {
  color: @brand-primary;
}